:root {
  --Soft-blue: hsl(215, 51%, 70%);
  --Cyan: hsl(178, 100%, 50%);
  --Very-dark-blue-main: hsl(217, 54%, 11%);
  --Very-dark-blue-card: hsl(216, 50%, 16%);
  --Very-dark-blue: hsl(215, 32%, 27%);
  --White: hsl(0, 0%, 100%);
}

:root {
  --ff: "Outfit", sans-serif;
  --fs-paragraph: 1.125rem;
  --fs-title: 1.375rem;
}

.flex {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.space {
  flex-grow: 1;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

img {
  display: block;
  max-width: 100%;
}

body {
  line-height: 1.5;
  font-family: var(--ff);
}

p {
  color: var(--Soft-blue);
  font-weight: 300;
}

.card__title {
  color: var(--White);
  font-size: var(--fs-title);
  width: fit-content;
  transition: all 0.5s ease;
}
.card__title:hover {
  color: var(--Cyan);
  cursor: pointer;
}
.card__paragraph {
  font-size: var(--fs-paragraph);
}
.card__ethereum {
  color: var(--Cyan);
  font-weight: 600;
}
.card__name {
  font-weight: 400;
}

.light {
  color: var(--White);
  transition: all 0.5s ease;
}

.light:hover {
  color: var(--Cyan);
  cursor: pointer;
}

body {
  width: min(85%, 90rem);
  margin-inline: auto;
  height: 100vh;
  display: grid;
  place-items: center;
  background-color: var(--Very-dark-blue-main);
}

.card {
  max-width: 21.875rem;
  padding: 1.5rem;
  background-color: var(--Very-dark-blue-card);
  border-radius: 0.75rem;
  box-shadow: 5px 5px 15px 5px rgba(0, 0, 0, 0.1);
}
.card__img {
  border-radius: 0.75rem;
  overflow: hidden;
  box-shadow: 5px 5px 15px 5px rgba(0, 0, 0, 0.1);
  margin-bottom: 1.25rem;
}
.card__title {
  margin-bottom: 0.625em;
}
.card__paragraph {
  margin-bottom: 1.25em;
}
.card__ethereum::before, .card__clock::before {
  position: relative;
  top: 2.5px;
  margin-right: 0.5rem;
}
.card__ethereum::before {
  content: url("../../images/icon-ethereum.svg");
}
.card__clock::before {
  content: url("../../images/icon-clock.svg");
}
.card__avatar {
  width: 2.175rem;
  border: 1px solid var(--White);
  border-radius: 50%;
}
.card__line {
  border-top: 1px solid var(--Very-dark-blue);
  padding-block: 1rem 0.625rem;
  margin-top: 1.25em;
}